html,
body {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #c6c6c0;
  font-family: 'Jost', sans-serif;
}

#root {
  width: 100%;
  height: 100%;
}


.App {
  text-align: center;
}

#loading {
  text-align: center;
  color: #6f570a;
  position: absolute;
  top: 50%;
  left: 50%;
}

#main {
  width: 100%;
  height: 100%;
}

#overlay {
  position: absolute;
  top: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;

  flex-direction: column;
  width: 100%;
  height: 100%;
  pointer-events: none;
  /* animation: blur 5s ease-in 0s;
  -webkit-animation: blur 5s ease-in 0s;
  -moz-animation: blur 5s ease-in 0s; */
  /* backdrop-filter: blur(5px); */

  overflow: hidden;
}

